body{
    margin: 0;
}


.flex{
    display: flex;
    transition: .3s;
    width: 100%;
    height: 100vh;
    flex-direction: column;
    border: 1px solid black;
    align-items: center;
    animation-name: ali1;
    animation-duration: .5s;
    background-color:white;
}

.trans {
    background-color:transparent;
}

.anc {
    color: black;
    cursor: pointer;
}

.mtop {
    margin-top: 200px;
}




.small{
    color: black;
    cursor: pointer;
    margin: auto 0;
}

.nav-item .active {
  font-weight: bold;
  text-decoration-line: underline;
  text-decoration-color: black;
  text-decoration-thickness: 2px;
  text-underline-offset: 3px;
}





@keyframes ali1 {
    from{
        width: 0;
    }
    to{
        width: 100%;
    }
}

.none {
  display: none;
}