.swiper-slide {
    text-align: center;
    font-size: 18px;
    background: #fff;
  
    /* Center slide text vertically */
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
  }

  

  .swiper-button-next {
    color: black !important;
  }

  .swiper-button-prev {
    color: black !important
  }

 
  img{
    width: 85%;
    height: 580px;
  }

  @media screen and (max-width:600px) {
    img {
      width: 100%;
      height: 420px;
    }
}

 