.animatcard {
    position: relative;
    animation-name: ali;
    animation-duration: 2s;
}

@keyframes ali {
    from {
        opacity: 0;   
    }
    to {
        opacity: 1;
    }
}


@media screen and (max-width:680px) {


    @keyframes ali {
        from {
            width: 0%;
            opacity: 0;   
        }
        to {
            width: 100%;
            opacity: 1;
        }
    }
}