.animates {
    position: relative;
    animation-name: ali;
    animation-duration: 2s;
}

@keyframes ali {
    from {
        opacity: 0;   
    }
    to {
        opacity: 1;
    }
}

.animator {
    opacity: 0;
    animation-name: ali2;
    animation-duration: 2s;  
}

@keyframes ali2 {
    from{
        width: 100%;
        opacity: 1;
    }
    to{
        width: 0%;
        opacity: 0;
        display: none;
    } 
}



@media screen and (max-width:680px) {
    .textanimate{
        animation-name: text1;
        animation-duration: 2s;
    }
    
    @keyframes text1 {
        from {
            height: 30%;
            opacity: 0;
        }
        to {
            width: 100%;
            opacity: 1;
        }
    }


    @keyframes ali {
        from {
            width: 0%;
            opacity: 0;   
        }
        to {
            width: 100%;
            opacity: 1;
        }
    }
}




